@import url('https://fonts.googleapis.com/css?family=Poppins:100,200,300,400,500,600,700,800,900|Roboto:100,300,400,400i,500,500i,700,700i,900,900i&display=swap');
$min_width: 320px;
$max_width: 1200px;

@font-face {
  font-family: 'BLOKK';
  src: url('/src/fonts/BLOKKNeue-Regular.eot');
  src: url('/src/fonts/BLOKKNeue-Regular.eot?#iefix')
      format('embedded-opentype'),
    url('/src/fonts/BLOKKNeue-Regular.woff2') format('woff2'),
    url('/src/fonts/BLOKKNeue-Regular.woff') format('woff'),
    url('/src/fonts/BLOKKNeue-Regular.otf') format('opentype'),
    url('/src/fonts/BLOKKNeue-Regular.ttf') format('truetype'),
    url('/src/fonts/BLOKKNeue-Regular.svg#BLOKKRegular') format('svg');
  font-weight: normal;
  font-style: normal;
}

@media screen and (-webkit-min-device-pixel-ratio: 0) {
  @font-face {
    font-family: 'BLOKK';
    src: url('/src/fonts/BLOKKNeue-Regular.svg') format('svg');
  }
}

@function strip-unit($value) {
  @return $value / ($value * 0 + 1);
}

@mixin fluid-type($min-vw, $max-vw, $min-font-size, $max-font-size) {
  $u1: unit($min-vw);
  $u2: unit($max-vw);
  $u3: unit($min-font-size);
  $u4: unit($max-font-size);

  @if $u1 == $u2 and $u1 == $u3 and $u1 == $u4 {
    & {
      font-size: $min-font-size;
      @media screen and (min-width: $min-vw) {
        font-size: calc(
          #{$min-font-size} + #{strip-unit($max-font-size - $min-font-size)} *
            ((100vw - #{$min-vw}) / #{strip-unit($max-vw - $min-vw)})
        );
      }
      @media screen and (min-width: $max-vw) {
        font-size: $max-font-size;
      }
    }
  }
}

$headline: 'poppins';
$body: 'roboto';
